<template>
  <div>
    <div class="classements" style="width:100%;">
      <div class="classement-head" style="width:100%;">
        <div style="width:10%;display:table-cell;float:right;">
          +/-
        </div>
        <div style="width:10%;display:table-cell;float:right;">
          P.
        </div>
        <div style="width:10%;display:table-cell;float:right;">
          N.
        </div>
        <div style="width:10%;display:table-cell;float:right;">
          G.
        </div>
        <div style="width:10%;display:table-cell;float:right;">
          J.
        </div>
        <div style="width:10%;display:table-cell;float:right;">
          Pts
        </div>
        <div style="width:40%;display:table-cell;float:right;visibility:hidden;">
          scs
        </div>
      </div>
      <br>
      <div class="classement-item" v-for="classement in classements.classements">
        <div style="width:5%;display:table-cell;">
          {{classement.rang}}
        </div>
        <div style="width:5%;display:table-cell;">
          <img v-bind:src="getLogoClub(classement.logo)" class="logo">
        </div>
        <div style="width:30%;display:table-cell;">
          {{classement.club}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.pt}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.J}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.G}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.N}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.P}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.somme}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
var debounce = require('lodash.debounce')
export default {
  name: 'classements',

  data () {
    return {
      classements: [],
      classement: ''
    }
  },
  methods: {
    getLogoClub (value) {
      return 'static/img/clubs/' + value
    },
    update () {
      this.classements = this.$store.state.classements
    },
    updateClassements: debounce(function () {
      this.update()
    }, 10000)
  },

  watch: {
    classements: function () {
      this.updateClassements()
    }
  },

  created: function () {
    window.addEventListener('online', this.updateClassements)
  },

  mounted: function () {
    this.update()
  }
}
</script>

<style>
.classement-head{
  text-align: center;
  vertical-align: middle;
  min-height: 10px;
}
.classement-item{
  border-bottom: 1px solid;
  text-align: center;
  vertical-align: middle;
  min-height: 30px;
}
</style>



